@import url(https://fonts.googleapis.com/css?family=Big+Shoulders+Text&display=swap);
body {
  margin: 0;
  font-family: 'Big Shoulders Text', cursive;
}

h1, h2, h3, h4, h5, p, a {
  font-family: 'Big Shoulders Text', cursive !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.route-wrapper {
  position: absolute;
}

.route-wrapper > div {
  position: absolute;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.wrapperHome {
  margin: 0;
  width: 100%;
  height: 100vh;
}

.wrapperHome .row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  padding: 0;
  margin: 0;
}

.wrapperHome .column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
  background: #000000;
}

.wrapperHome .column.contenido {
  flex: 12 1;
  margin: 0;
  padding: 0;
}

.wrapperHome .blue-column {
  height: 100vh;
  display: flex;
  align-items: center;
  background: #111;
  justify-content: center;
  box-shadow: 0 0 10px inset #000000;
}

.wrapperHome .blue-column i.material-icons {
  font-size: 2em;
  color: #ffffff;
  margin: 20px 0;
  font-size: 1.2em;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.wrapperHome .blue-column i.material-icons:hover {
  color: #ff0000;
}

.wrapperHome .green-column {
  height: 100vh;
  display: flex;
  align-items: center;
  text-align: left;
  padding: 20px;
  color: #333;
}

.wrapperHome .green-column a {
  color: #000000;
}

.menu ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.menu ul li a {
    text-decoration: none;
    color: #ffffff;
    -webkit-transition: all 1s;
    transition: all 1s;
    font-weight: 100;
}

.menu a:hover {
    color: #cccccc;
}

.column.contenido {
    background: rgb(20,20,20);
background: radial-gradient(circle, rgba(20,20,20,1) 0%, rgba(0,0,0,1) 100%);
    position: absolut;
}

.column.contenido h1 {
    color: #ffffff;
    font-size: 2em;
}

.column.contenido h2 {
    color: #dedede;
}

.column.contenido p {
    color: #888888;
}

.lni-angle-double-right {
    cursor: pointer;
    -webkit-transition: all 1s;
    transition: all 1s;
}

.lni-angle-double-right:hover {
    color: red;
}
